.dissertation-subtitle {
  font-size: 1.35rem;
}

.dissertation-wrapper {
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: $black-025;
  border-radius: 16px;
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
}

.dissertation-cover {
  flex: 1;
  display: flex;
  align-items: center;
}

.dissertation-cover > a > img {
  border: 1px solid;
  border-color: $border-color;
}

.dissertation-cover > a > img:hover {
  border-color: $dark-gray;
}

.dissertation-info {
  flex: 3;
  display: flex;
  align-items: center;
}

.dissertation-info > div > p {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}

.document-metadata {
  font-size: 0.75rem;
  font-weight: 600;
}

.iii {
  width: 50%;
  margin: 0 auto;
}

.sc {
  font-variant: small-caps;
}

.talk-wrapper {
  display: flex;
  flex-direction: column;
  // gap: 1rem;
  padding-top: 1rem;
}

.talk-video {
  flex: 1.6;
}

.talk-sidebar {
  flex: 1;
  padding-top: 0rem;
  padding-bottom: 1rem;
  padding-right: 0rem;
  padding-left: 0rem;
}

.talk-sidebar > ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0.5rem;
}

.talk-sidebar > ul > li {
  border-radius: 0.5rem;
  // padding: 0.3rem 0.5rem;
  padding-right: 2rem;
  margin: 0.3rem 0;
  background-color: $black-025;
  display: flex;
  flex-direction: row;
  cursor: pointer;
  column-gap: 0.75rem;
  font-weight: 500;
  transition: all .2s ease-in-out;
}

.talk-sidebar > ul > li:hover {
  background-color: $black-10;
  transform: scale(1.02)
}

.talk-thumbnail {
  width: 5.5rem;
  flex-basis: 5.5rem;
	flex-grow: 0;
	flex-shrink: 0;
  background-color: gray;
  border-radius: 0.35rem;
  border: 3px solid #222222;
  margin: 0.25rem;
}

.talk-sidebar-timestamp {
  display: flex;
  align-items: center;
  font-size: small;
}

.talk-sidebar-timestamp-badge {
  color: $blue;
  background-color: $intro-blue;
  padding: 2px;
  border-radius: 4px;
}

@media screen and (max-width: $mobile) {
  .dissertation-cover {
    flex: 1;
  }

  .talk-wrapper {
    flex-direction: column;
  }

  .talk-video {
    flex: 1;
  }

  .talk-sidebar {
    flex: 1;
  }

  .talk-sidebar > ul {
    grid-template-columns: 1fr;
    grid-column-gap: 0.5rem;
  }

}